}

@keyframes SlideOut {
  from{
    transform: translateY(0);
    opacity: 1;
  }

  to{
    transform: translateY(-20px);
    opacity: 0;
  }
}

@keyframes SlideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

